}

.home-heading {
  font-weight: 600;
  font-size: 3rem;
  color: #b4a5a5;
}

.home-title {
  font-weight: 00;
  font-size: 2rem;
  letter-spacing: 1.5px;
  background: #e2703a;
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}
.home-subtitle {
  font-weight: 300;
  max-width: 700px;
  font-size: 1.2rem;
  letter-spacing: 1px;
  color: #b4a5a5;
}

.home-img-wrapper {
  max-width: 700px;
  margin-top: 120px;
  margin-left: 80px;
  width: 100%;
  height: auto;
}

span {
  color: #ff6363;
  font-family: "Dancing Script", cursive;
  letter-spacing: 5px;
  font-weight: 700;
  font-size: 4rem;
  -webkit-text-decoration: 5px underline;
          text-decoration: 5px underline;
  text-underline-position: under;
  -webkit-text-decoration-color: #ff6363;
          text-decoration-color: #ff6363;
}

.icon-bar {
  position: fixed;
  top: 55%;
  margin-left: 100px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 1;
}

.icon-bar a {
  display: block;
  text-align: center;
  padding: 16px;
  transition: all 0.3s ease;
  color: #fff;
  font-size: 1.5rem;
  margin-bottom: 75px;
}

.github {
  background: transparent;
  color: fff;
}
.github:hover {
  background: #000;
  color: #b4a5a5;
}

.google {
  background: transparent;
  color: #fff;
}
.google:hover {
  background: #dd4b39;
  color: #b4a5a5;
}

.linkedin {
  background: transparent;
  color: #fff;
}
.linkedin:hover {
  background: #007bb5;
  color: #b4a5a5;
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (max-width: 1199px) {
  img {
    display: none;
  }

  .icon-bar {
    display: none;
  }

  .home-section {
    padding: 160px 0;
  }
}

@media only screen and (max-width: 375px) {
  .home-section {
    padding: 85px 0;
  }
}

