  min-width: 170px;
  height: 200px;
  margin: 25px 15px 25px 15px;
  box-shadow: 0px 0px 2px 2px #e2703a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 15px;
  line-height: 25px;
  background-color: transparent;
  font-family: "Montserrat";
  border-radius: 10px;
  cursor: pointer;
}

